.headerWrap {
    position: fixed; top: 0px; left: 0px;
    color: #fff;
    width: 100%;
    transition: all 0.5s;
    z-index: 9;
    .row {
        // border: 1px solid #fff;
        display: flex;
        height: 80px;
        justify-content: space-between;
        position: relative;
        align-items: center;
        // .logo, .nav, .mobileNav {align-self: center;}
        .mobileNav {
            position: relative;
            button {
                padding: 10px 20px;
                border-radius: 50px; border: none;
                font-size: 20px;
                display: flex;
                cursor: pointer;
                .ham {
                    margin-right: 10px;
                }
                .ham, .person {
                    align-self: center;
                }
            }
            .mobileNavList {
                position: absolute; top: 100%; right: 0px;
                background: #fff; color: #000;
                padding: 10px 0px; margin-top: 15px;
                width: 150px;
                border-radius: 10px;
                display: none;
                &.opener {display: block;}
                ul {
                    li {
                        line-height: 30px;
                        padding: 0px 20px;
                    }
                    li:nth-child(3) {
                        border-top: 1px solid #ddd;
                        padding-top: 10px; margin-top: 10px;
                    }
                }
            }
        }
        .nav {
            position: absolute; left: 50%;
            transform: translateX(-50%);
            font-size: 20px;
            .depth1 {
                display: flex;
                li {
                    position: relative;
                    padding: 20px 0px; margin: 0px 20px;
                    cursor: pointer;
                    &:hover {
                        color: #333;
                    }
                    .depth2 {
                        position: absolute; top: 100%; left: 50%;
                        transform: translate(-50%);
                        width: 150px;
                        background: #fff; color: #000;
                        border-radius: 10px; border: 2px solid #ccc;
                        display: none;
                        text-align: center;
                        &.on {display: block;}
                        li {
                            padding: 10px 0px; margin: 0px;
                            border-bottom: 2px solid #ccc;
                            font-size: 16px;
                        }
                        li:nth-child(3) {
                            border-bottom: none;
                        }
                    }
                }
            }
        }
    }
}