.homesecWrap {
    
    .article2 {
        .row {
            ul {
                display: flex; flex-wrap: wrap;
                padding: 30px 0px;
                li {
                    width: 33.33%;
                    padding: 20px;
                    a {
                        display: flex;
                        img {
                            width: 30%; min-width: 85px; min-height: 85px;
                            margin-right: 10px;
                            border-radius: 10px; border: 5px solid #bbb;
                        }
                        div {
                            align-self: center;
                            strong {
                                font-size: 20px;
                            }
                            p {
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
            .lifeStyle {
                h2 {
                    font-size: 30px;
                    padding-left: 10px;
                }
                ul {
                    display: flex;
                    li {
                        width: 33.33%;
                        text-align: center;
                        padding: 0px 10px;
                        img {
                            width: 100%;
                            border-radius: 10px;
                            margin-bottom: 10px;
                        }
                        strong {
                            font-size: 21px;
                        }
                    }
                }
            }
        }
    }
    .article3 {
        background: #000; color: #fff;
        padding: 100px 0px;
        .row {
            display: flex;
            div {
                img {
                    width: 100%;
                    padding: 10px 10px 0px;
                    border-radius: 30px 30px 0px 0px;
                    vertical-align: middle;
                }
                p {
                    background: #fff; color: #333;
                    font-weight: bold;
                    margin: 0px 10px; padding: 5px 10px 5px 15px;
                    border-radius: 0px 0px 30px 30px;
                    font-size: 20px;
                    display: -webkit-box; -webkit-line-clamp: 1; -webkit-box-orient: vertical; overflow: hidden;
                    // 약간 편법인데... 위에 1줄 쓰면 리액트에서는 1줄 이상 다 보이나봄
                    line-height: 1.5em;
                    // 그래서 라인하이트로 밑에줄을 더 밑으로 보내서 안보이게 만들기.. 이래도 되나..
                }
                &.left {
                    width: 52%;
                }
                &.right {
                    width: 48%;
                    display: flex; flex-direction: column;
                    justify-content: space-between;
                    .rightFst {
                        display: flex; flex-direction: row;
                        // width: 100%;
                    }
                    div {
                        width: 100%;
                        // p {
                        //     text-overflow: ellipsis; overflow: hidden; white-space: nowrap;
                        // }
                    }
                }
            }
        }
    }
}