@font-face {
  font-family: 'GabiaSolmee';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_one@1.0/GabiaSolmee.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
* {margin: 0px; padding: 0px; box-sizing: border-box; outline: none; font-family: 'GabiaSolmee';}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {color: inherit; text-decoration: none;}
ul, ol {list-style: none;}
.row {max-width: 1600px; margin: 0px auto; padding: 0px 80px;}
.cf::after {content: ""; display: block; clear: both;}
img {width: 100%; vertical-align: middle;}
button {cursor: pointer; border: none;}
fieldset {display: none;}
legend {display: none;}
h1, h2, h3, h4, h5, h6 {font-size: 100%;}
table {width: 100%; border-collapse: collapse; table-layout: fixed;}
.blind {position: absolute; width: 1px; height: 1px; overflow: hidden; text-indent: -9999999px;}
